<template>
  <div class="c-LotteryConfig">
    <div class="container">
      <div class="form-data">
        <div class="form-data--line">
          <div class="form-data--label">抽奖标题</div>
          <div class="form-data--input">
            <el-input
              v-model="form.name"
              placeholder="请输入抽奖的标题"
            ></el-input>
          </div>
        </div>
        <div class="form-data--line">
          <div class="form-data--label">抽奖总人数</div>
          <div class="form-data--input">
            <el-input
              type="text"
              v-model.trim.number="form.number"
              placeholder="请输入总人数"
            ></el-input>
          </div>
        </div>
        <div class="form-data--line">
          <div class="form-data--label">奖项一</div>
          <div class="form-data--input">
            <el-input
              type="text"
              v-model.trim.number="form.firstPrize"
              placeholder="请输入获奖人数"
            ></el-input>
          </div>
        </div>
        <div
          v-for="newitem in storeNewLottery"
          class="form-data--line"
          :key="newitem.key"
        >
          <div class="form-data--label">{{newitem.name}}</div>
          <div class="form-data--input">
            <el-input
              type="text"
              v-model.trim.number="form[newitem.key]"
              placeholder="请输入获奖人数"
              @change="
              val => {
                form[newitem.key] = Number(val);
              }
            "
            ></el-input>
          </div>
        </div>
      </div>
    </div>
    <div class="dialog-menus">
      <!-- 保存配置 -->
      <span class="menu"><img
          src="@/assets/images/saveConfig-btn_small.png"
          alt=""
          @click="onSubmit"
        ></span>
      <span class="menu"><img
          src="@/assets/images/prizeList-btn_small.png"
          alt=""
          @click="handlePrize"
        ></span>
      <!-- 取消 -->
      <span class="menu menu__cancel"><img
          src="@/assets/images/cancel-btn_small.png"
          alt=""
          @click="closeDialog"
        ></span>
    </div>
    <el-dialog
      :visible.sync="showAddLottery"
      :append-to-body="true"
      width="300px"
      class="dialog-showAddLottery"
    >
      <div
        class="add-title"
        slot="title"
      >增加奖项</div>
      <el-form
        ref="newLottery"
        :model="newLottery"
        size="mini"
      >
        <el-form-item label="奖项名称">
          <el-input v-model="newLottery.name"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            @click="addHandler"
          >增加奖项</el-button>
          <el-button @click="showAddLottery = false">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
  <!-- </el-dialog> -->
</template>
<script>
import { setData, configField } from '@/helper/index';
import { randomNum } from '@/helper/algorithm';
export default {
  name: 'LotteryConfig',
  props: {
    visible: Boolean,
  },
  computed: {
    // form: {
    //   get() {
    //     return this.$store.state.config;
    //   },
      // set(val) {
      //   // this.$store.commit('setConfig', val);
      //   return val;
      // },
    // },
    storeNewLottery() {
      return this.$store.state.newLottery;
    },
    getPrizeIsNull() {
      return this.$store.getters.getPrizeNull;
    },
  },
  data() {
    return {
      showAddLottery: false,
      newLottery: { name: '' },
      form: {}
    };
  },
  created(){
    this.form = JSON.parse(JSON.stringify(this.$store.state.config));
  },
  methods: {
    onSubmit() {
      if (!this.getPrizeIsNull) {
        this.saveConfig();
        return;
      }
      this.$confirm('奖品所对应的奖项未填写完整，是否不填?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(() => {
          this.saveConfig();
        })
        .catch(() => {});
    },
    saveConfig() {
      setData(configField, this.form);
      this.$store.commit('setConfig', this.form);
      // this.$emit('update:visible', false);
      this.closeDialog();

      this.$message({
        message: '保存成功',
        type: 'success',
      });

      this.$nextTick(() => {
        this.$emit('resetconfig');
      });
    },
    handlePrize() {
      this.$emit('handlePrize', true);
    },
    addLottery() {
      this.showAddLottery = true;
    },
    randomField() {
      const str = 'abcdefghijklmnopqrstuvwxyz';
      let fieldStr = '';
      for (let index = 0; index < 10; index++) {
        fieldStr += str.split('')[randomNum(1, 27) - 1];
      }
      return `${fieldStr}${Date.now()}`;
    },
    addHandler() {
      const field = this.randomField();
      const data = {
        key: field,
        name: this.newLottery.name,
      };
      this.$store.commit('setNewLottery', data);

      this.showAddLottery = false;
    },
    closeDialog() {
      this.$emit('closeDialog');
    },
  },
};
</script>
