import Vue from 'vue';
import Vuex from 'vuex';
import {
  setData,
  resultField,
  newLotteryField,
  prizeField,
  listField
} from '@/helper/index';

Vue.use(Vuex);
function getConfig() {
  return JSON.parse(
    JSON.stringify({
      config: {
        name: "月月们的\"云\"圣诞节",
        number: 5,
        firstPrize: 1,
        secondPrize: 1,
        threePrize: 1,
        fourPrize: 2,
      },
      result: {
        firstPrize: [],
        secondPrize: [],
        threePrize: [],
        fourPrize: [],
      },
      prize: {
        firstPrize: [],
        secondPrize: [],
        threePrize: [],
        fourPrize: [],
      },
      newLottery: [
        { key: 'secondPrize', name: '奖项二' },
        { key: 'threePrize', name: '奖项三' },
        { key: 'fourPrize', name: '奖项四' }
      ],
      list: [],
      photos: []
    })
  );
}
export default new Vuex.Store({
  state: {
    config: {
      name: "月月们的\"云\"圣诞节",
      number: 165,
      firstPrize: 10,
      secondPrize: 5,
      threePrize: 5,
      fourPrize: 5,
    },
    result: {
      firstPrize: [],
      secondPrize: [],
      threePrize: [],
      fourPrize: [],
    },
    prize: {
      firstPrize: [], // { id: 1, name: '苹果耳机2代', userId: null }
      secondPrize: [],
      threePrize: [],
      fourPrize: [],
    },
    newLottery: [
      { key: 'secondPrize', name: '奖项二' },
      { key: 'threePrize', name: '奖项三' },
      { key: 'fourPrize', name: '奖项四' }
    ],
    list: [],
    photos: []
  },
  mutations: {
    setClearConfig(state) {
      const stateConfig = getConfig();
      state.config = stateConfig.config;
      state.newLottery = stateConfig.newLottery;
      // state.config = {
      //   name: '年会抽奖',
      //   number: 306,
      //   firstPrize: 1
      // };
      // state.newLottery = [
      //   { key: 'secondPrize', name: '二等奖' },
      //   { key: 'threePrize', name: '三等奖' }
      // ];
    },
    setClearList(state) {
      const stateConfig = getConfig();
      state.list = stateConfig.list;
    },
    setClearPhotos(state) {
      const stateConfig = getConfig();
      state.photos = stateConfig.photos;
      // state.photos = [];
    },
    setClearResult(state) {
      const stateConfig = getConfig();
      state.result = stateConfig.result;

      // 清空奖品对应的人
      const tempPrize = {};
      for (let key in state.prize) {
        const clearPrize = state.prize[key].map(prize => {
          return { id: prize.id, name: prize.name, userId: null };
        });
        tempPrize[key] = clearPrize;
      }
      state.prize = tempPrize;
      setData(prizeField, state.prize);
    },
    setClearStore(state) {
      const stateConfig = getConfig();
      state.config = stateConfig.config;
      state.result = stateConfig.result;
      state.newLottery = stateConfig.newLottery;
      state.list = stateConfig.list;
      state.photos = stateConfig.photos;
      // state.config = {
      //   name: '年会抽奖',
      //   number: 306,
      //   firstPrize: 1
      // };
      // state.result = {
      //   firstPrize: []
      // };
      // state.newLottery = [
      //   { key: 'secondPrize', name: '二等奖' },
      //   { key: 'threePrize', name: '三等奖' }
      // ];
      // state.list = [];
      // state.photos = [];
    },
    setDrawNumber(state, num) {
      state.config.number = num;
    },
    setConfig(state, config) {
      state.config = config;
    },
    setPrize(state, prize) {
      state.prize = prize;
      setData(prizeField, state.prize);
    },
    setResult(state, result = {}) {
      state.result = result;

      setData(resultField, state.result);
    },
    setNewLottery(state, newLottery) {
      if (state.newLottery.find(item => item.name === newLottery.name)) {
        return;
      }
      state.newLottery.push(newLottery);
      setData(newLotteryField, state.newLottery);
    },
    setList(state, list) {
      // const arr = state.list;
      // list.forEach(item => {
      //   const arrIndex = arr.findIndex(data => data.key === item.key);
      //   if (arrIndex > -1) {
      //     arr[arrIndex].name = item.name;
      //   } else {
      //     arr.push(item);
      //   }
      // });
      state.list = list;

      setData(listField, list);
    },
    setPhotos(state, photos) {
      state.photos = photos;
    }
  },
  actions: {},
  getters: {
    getPrizeNull(state) {
      const config = state.config;
      let isNull = false;
      for (let key in state.prize) {
        if (state.prize[key].length < config[key]) {
          isNull = true;
        }
        if (!isNull) {
          const nullPrize = state.prize[key].find(prize => !prize.name);
          if (nullPrize) {
            isNull = true;
          }
        }
      }
      return isNull;
    }
  },
  modules: {}
});
