<template>
  <div
    class="my-dialog"
    v-show="visible"
  >
    <div class="my-dialog--container" :style="{
        backgroundImage:`url(${require('../assets/images/'+bgImg)})`,
    }">
      <span class="my-dialog--close" @click="closeDialog"></span>
      <div class="my-dialog--container__panel">
          <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    visible: {
      type: Boolean,
      default: true,
    },
    bgImg: {
        type: String,
        default: 'setting-dialog.png'
    }
  },
  methods: {
      closeDialog(){
          this.$emit('update:visible', false)
      }
  }
};
</script>

<style lang="scss" scoped>
.my-dialog {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  //   background: #1c0000;
  //   opacity: 0.5;
  background: rgba(28, 0, 0, 0.5);
  z-index: 2000;
  &--container {
    position: relative;
    top: 50%;
    width: 580px;
    height: 705px;
    margin: 0 auto;
    // background-image: url('../assets/images/setting-dialog.png');
    background-size: 100% 100%;
    background-position: center center;
    background-repeat: no-repeat;
    transform: translateY(-50%);
    &__panel {
      position: absolute;
      top: 146px;
      left: 50%;
      width: 420px;
    //   height: 470px;
    //   border: 1px solid black;
      transform: translateX(-50%);
    }
  }
  &--close {
    position: absolute;
    top: 10px;
    right: 20px;
    display: block;
    width: 40px;
    height: 40px;
    background-image: url('../assets/images/close-btn.png');
    background-size: 100% 100%;
    cursor: pointer;
  }
}
</style>
