  <div id="tool">
    <div class="tool-menus">
      <div class="menus--settings">
        <img
          src="@/assets/images/importOrder-btn.png"
          alt=""
          @click="showImport = true"
        >
        <img
          src="@/assets/images/resetOption-btn.png"
          alt=""
          @click="showRemoveoptions = true"
        >
      </div>
      <div class="menus--lottery">
        <img
          v-if="!running"
