          >
            {{item.label}}
          </span>
        </div>
        <div class="startLottery__container">
          <template v-if="!isLuckEnd">
            <div class="describe">
              <span class="describe__text">即将抽取第</span>
              <div class="describe__count"><span class="describe__count--active">{{ getOverCount }}</span><span>/{{config[form.category]}}</span></div>
              <span class="describe__text">位中奖者</span>
            </div>
            <!-- <div class="form__line">
            <span class="form__label">抽取方式</span>
            <span class="form__value">
            <el-select
              v-model="form.mode"
              placeholder="请选取本次抽取方式"
            >
              <el-option
                label="抽1人"
