1 张三 staffPhoto/图片.png
2 李四 staffPhoto/图片.png
3 王五 staffPhoto/图片.png
				"
            v-model="listStr"
          ></el-input>
          <el-input
            v-else
            type="textarea"
            class="textArea__input"
            :rows="10"
            placeholder='请通过JSON格式导入数据，如：
        [
        {"id":"1","name":"张三","value":"staffPhoto/图片.png"}
        ]
        '
            v-model="JsonConfig"
          ></el-input>
        </div>
        <div class="importMenus">
          <!-- 确定 -->
          <img
            src="@/assets/images/sure-btn.png"
            alt=""
            @click="transformList"
          >
          <!-- 切换 -->
          <img
            src="@/assets/images/switch-btn.png"
            alt=""
            @click="isByExcel = !isByExcel"
          >
          <!-- 取消 -->
          <img
            src="@/assets/images/importCancel-btn.png"
            alt=""
            @click="showImport = false"
          >
        </div>
      </div>
    </MyDialog>
