<script>
import {
  clearData,
  removeData,
  configField,
  // listField,
  setData,
  resultField,
  conversionCategoryName,
} from '@/helper/index';
import Importphoto from './Importphoto';
import { database, DB_STORE_NAME } from '@/helper/db';
import config from '@/utils/config.json';
import MyDialog from '@/components/MyDialog';

export default {
  components: { Importphoto, MyDialog },
  props: {
    running: Boolean,
    closeRes: Function,
  },
  computed: {
    config: {
      get() {
        return this.$store.state.config;
      },
    },
    remain() {
      return (
        this.config[this.form.category] -
        (this.result[this.form.category]
          ? this.result[this.form.category].length
          : 0)
      );
    },
    getOverCount() {
      const currentResult = this.result[this.form.category];
      const total = this.config[this.form.category];
      if (currentResult.length >= total) {
        return total;
      }
      return currentResult ? currentResult.length + 1 : 1;
    },
    result() {
      return this.$store.state.result;
    },
    isLuckEnd() {
      return (
        this.result[this.form.category].length >=
        this.config[this.form.category]
      );
    },
    categorys() {
      const options = [];
      for (const key in this.config) {
        if (this.config.hasOwnProperty(key)) {
          const item = this.config[key];
          if (item > 0) {
            let name = conversionCategoryName(key);
            name &&
              options.push({
                label: name,
                value: key,
              });
          }
        }
      }
      return options;
    },
  },
  data() {
    return {
      showSetwat: false,
      showImport: false,
      showImportphoto: false,
      showRemoveoptions: false,
      showCheckData: false,
      removeInfo: { type: 0 },
      form: {
        category: 'firstPrize', // 默认一等奖
        mode: 1,
        qty: 1,
        allin: false,
      },
      listStr: '',
      isByExcel: true, // 通过Excel粘贴导入
      JsonConfig: JSON.stringify(config),
      isLoading: false,
      resetOption: [
        { label: '重置名单', value: 2 },
        { label: '重置照片', value: 3 },
        { label: '重置抽奖结果', value: 4 },
      ],
    };
  },
  created() {
    // 加载数据
    // this.JsonConfig = JSON.stringify(config);
    // let target = JSON.parse(this.JsonConfig);
    // target = target.map((d) => {
    //   return `${d.id} ${d.name} ${d.value}`;
    // });
    // this.importData(target);
  },
  watch: {
    showRemoveoptions(v) {
      if (!v) {
        this.removeInfo.type = 0;
      }
    },
  },
  methods: {
    switchOption(option) {
      this.removeInfo.type = option.value;
    },
    resetConfig() {
      const type = this.removeInfo.type;
      this.$confirm('此操作将重置所选数据，是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(() => {
          let target = null;
          const _this = this;
          switch (type) {
            case 0:
              clearData();
              this.$store.commit('setClearStore');
              database.clear(DB_STORE_NAME);
              break;
            case 1:
              removeData(configField);
              this.$store.commit('setClearConfig');
              break;
            case 2:
              // 重置名单
              // removeData(listField);
              // this.$store.commit('setClearList');
              database.clear(DB_STORE_NAME)
              removeData(configField);
              this.$store.commit('setClearConfig');
              _this.JsonConfig = JSON.parse(JSON.stringify(config));
              if (_this.JsonConfig.length > 0) {
                const currentConfig = Object.assign(
                  {},
                  { ...this.config },
                  { number: _this.JsonConfig.length }
                );
                setData(configField, currentConfig);
                this.$store.commit('setConfig', currentConfig);
              }
              target = JSON.parse(JSON.stringify(_this.JsonConfig));
              target = target.map((d) => {
                return `${d.id} ${d.name} ${d.value}`;
              });
              this.importData(target);
              break;
            case 3:
              database.clear(DB_STORE_NAME);
              this.$store.commit('setClearPhotos');
              break;
            case 4:
              removeData(resultField);
              this.$store.commit('setClearResult');
              break;
            default:
              break;
          }

          this.closeRes && this.closeRes();

          this.showRemoveoptions = false;
          this.$message({
            type: 'success',
            message: '重置成功!',
          });
          if(type !==4){
            this.$nextTick(() => {
              this.$emit('resetConfig');
            });
          }
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消',
          });
        });
    },
    handleTab(category) {
      this.form.category = category.value;
    },
    onSubmit() {
      if (!this.form.category) {
        return this.$message.error('请选择本次抽取的奖项');
      }
      if (this.remain <= 0) {
        return this.$message.error('该奖项剩余人数不足');
      }
      if (this.form.mode === 99) {
        if (this.form.qty <= 0) {
          return this.$message.error('必须输入本次抽取人数');
        }
        if (this.form.qty > this.remain) {
          return this.$message.error('本次抽奖人数已超过本奖项的剩余人数');
        }
      }
      if (this.form.mode === 1 || this.form.mode === 5) {
        if (this.form.mode > this.remain) {
          return this.$message.error('本次抽奖人数已超过本奖项的剩余人数');
        }
      }
      this.showSetwat = false;
      this.$emit(
        'toggle',
        Object.assign({}, this.form, { remain: this.remain })
      );
    },
    startHandler() {
      this.$emit('toggle');
      if (!this.running) {
        this.showSetwat = true;
      }
    },
    async transformList() {
      if (!this.isByExcel) {
        // 通过json格式导入
        let target = null;
        target = JSON.parse(this.JsonConfig);
        target = target.map((d) => {
          return `${d.id} ${d.name} ${d.value}`;
        });
        await this.importData(target);
        this.showImport = false;
        this.$nextTick(() => {
          this.$emit('resetConfig');
        });
        return;
      }
      // 粘贴方式导入
      const { listStr } = this;
      if (!listStr) {
        this.$message.error('没有数据');
      }

      const rows = listStr.split('\n');
      if (rows && rows.length > 0) {
        await this.importData(rows);
      }

      this.showImport = false;
      this.$nextTick(() => {
        this.$emit('resetConfig');
      });
    },
    async importData(rows) {
      this.isLoading = true;
      const list = [];
      await Promise.all(
        rows.map(async (item) => {
          const rowList = item.split(/\t|\s/);
          if (rowList.length >= 2) {
            const key = Number(rowList[0].trim());
            const name = rowList[1].trim();
            key &&
              list.push({
                key,
                name,
              });
            // 有图片放置数据库中
            if (rowList[2]) {
              const ID = key;
              const Data = await database.get(DB_STORE_NAME, ID);
              const param = {
                id: ID,
                value: rowList[2],
              };
              return database[Data ? 'edit' : 'add'](
                DB_STORE_NAME,
                Data ? ID : param,
                Data ? param : null
              );
            }
          }
        })
      )
        .then(() => {
          // 更新photos
          this.$emit('getPhoto');
          this.$store.commit('setList', list);

          this.$message({
            message: '保存成功',
            type: 'success',
          });
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
  },
};
</script>
