<template>
  <div class="c-Result">
    <div
      class="result--title"
      slot="title"
    >
      <span
        v-for="(item, index) in resultList"
        class="result--title-tab"
        :class="{'result--title-tab__active':index===currentTabIndex}"
        :key="index"
        @click="handleTab(index)"
      >
        {{item.name}}
      </span>
      <!-- <span :style="{ fontSize: '18px' }">
        抽奖结果
      </span>
      <span :style="{ fontSize: '14px', color: '#999', marginLeft: '10px' }">
        (点击号码可以删除)
      </span> -->
    </div>
    <!-- <div
      v-for="(item, index) in resultList"
      :key="index"
      class="listrow"
      @click="
        event => {
          deleteRes(event, item);
        }
      "
    >
      <span class="name">
        {{ item.name }}
      </span>
      <span class="value">
        <span v-if="item.value && item.value.length === 0">
          暂未抽奖
        </span>
        <span
          class="card"
          v-for="(data, j) in item.value"
          :key="j"
          :data-res="data"
        >
          {{ getResultUser(data).name }}
        </span>
      </span>
    </div> -->
    <div class="resultList">
      <ul
        v-for="(group,index) of getGroupList"
        class="resultList--line"
        :class="{'resultList--line__center': getGroupCount===1}"
        :key="index"
      >
        <!-- :class="{'resultList--line__last':index===getGroupList.lenth&&index>5}" -->
        <template v-if="group">
          <li
            v-for="(user,n) of group"
            class="resultList--line-user"
            :class="{'resultList--line-user__center': getGroupCount===1}"
            :key="n"
          >
            <span v-if="!getCurrentPhoto(user.key)"></span>
            <img
              v-else
              :src="getCurrentPhoto(user.key).value"
              class="photo"
              alt=""
            >
            <div class="awardInfo">
              <span class="awardInfo__winner">{{user.name}}</span>
              <span
                class="awardInfo__prize"
                :title="getPrize(user.key)?getPrize(user.key).name:''"
              >{{getPrize(user.key)?getPrize(user.key).name:''}}</span>
            </div>
          </li>
        </template>
      </ul>
      <ul
        v-for="(item,index) of getEmptList"
        class="resultList--line resultList--line__center"
        :key="'empt'+index"
      >
        <li
          v-if="getGroupList.length<=0 && index === 2"
          class="resultList--line-user resultList--line-user__center"
        >敬请期待！</li>
      </ul>
    </div>
  </div>
</template>
<script>
import { conversionCategoryName, getDomData } from '@/helper/index';
export default {
  name: 'c-Result',
  props: {
    visible: Boolean,
  },
  data() {
    return {
      currentTabIndex: 0,
    };
  },
  computed: {
    photos() {
      return this.$store.state.photos;
    },
    config() {
      return this.$store.state.config;
    },
    result: {
      get() {
        return this.$store.state.result;
      },
      set(val) {
        this.$store.commit('setResult', val);
      },
    },
    resultList() {
      const list = [];
      for (const key in this.result) {
        if (
          this.result.hasOwnProperty(key) &&
          this.config.hasOwnProperty(key)
        ) {
          const item = this.config[key];
          if (item) {
            const element = this.result[key];
            let name = conversionCategoryName(key);
            list.push({
              label: key,
              name,
              value: element,
            });
          }
        }
      }
      return list;
    },
    list() {
      return this.$store.state.list;
    },
    prize() {
      return this.$store.state.prize;
    },

    getCurrentList() {
      if (!this.resultList[this.currentTabIndex]) return [];
      return this.resultList[this.currentTabIndex].value.map((id) => {
        return this.getResultUser(id);
      });
    },

    getGroupList() {
      const target = [];
      let temp = [];
      for (let i = 0; i < this.getCurrentList.length; i++) {
        const current = this.getCurrentList[i];
        if (temp.length < this.getGroupCount) {
          temp.push(current);
          if (
            temp.length >= this.getGroupCount ||
            i === this.getCurrentList.length - 1
          ) {
            target.push(temp);
            // 清空临时分组
            temp = [];
          }
        }
      }
      return target;
    },
    // 分组数量小于6，单行排列
    getGroupCount() {
      const key = this.resultList[this.currentTabIndex].label;
      return this.config[key] < 6 ? 1 : 2;
    },
    getEmptList() {
      let temp = 6 - this.getGroupList.length;
      temp = temp < 0 ? 0 : temp;
      return new Array(temp).fill('');
    },
  },
  methods: {
    handleTab(index) {
      this.currentTabIndex = index;
    },
    getCurrentPhoto(id) {
      return this.photos.find((p) => p.id === id);
    },
    getResultUser(value) {
      if (isNaN(value)) return value;
      return this.list.find((result) => result.key === Number(value));
    },
    getPrize(userId) {
      return this.prize[this.resultList[this.currentTabIndex].label].find(
        (prize) => prize.userId === userId
      );
    },
    deleteRes(event, row) {
      const Index = getDomData(event.target, 'res');
      if (!Index) {
        return;
      }
      this.$confirm('此操作将移除该中奖号码，确认删除?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(() => {
          if (Index) {
            const result = this.result;
            result[row.label] = this.result[row.label].filter(
              (item) => item !== Number(Index)
            );
            this.result = result;
            this.$message({
              type: 'success',
              message: '删除成功!',
            });
          }
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消',
          });
        });
    },
  },
};
</script>
