<template>
  <div class="prizeList">
    <div
      class="prizeList--title"
      slot="title"
    >
      <span
        v-for="(item, index) in categorys"
        class="prizeList--title-tab"
        :class="{'prizeList--title-tab__active':index===currentTabIndex}"
        :key="index"
        @click="handleTab(index)"
      >
        {{item.label}}
      </span>
    </div>
    <div class="prizeList__container">
      <div class="prizeList__form form">
        <div
          class="form__line"
          v-for="index of config[getCurrentTab.value]"
          :key="index"
        >
          <div class="form__label">第{{index}}个</div>
          <div class="form__input">
            <el-input
              v-model="form[getCurrentTab.value][`${index}`]"
              placeholder="请输入奖品"
            ></el-input>
          </div>
        </div>
      </div>
      <div class="prizeList__menus">
        <span class="menu">
          <img
            src="@/assets/images/saveBack-btn.png"
            alt=""
            @click="saveConfig"
          >
        </span><span class="menu">
          <img
            src="@/assets/images/cancel-btn.png"
            alt=""
            @click="closeDialog"
          >
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import { conversionCategoryName } from '@/helper/index';
export default {
  data() {
    return { currentTabIndex: 0, form: {} };
  },
  watch: {
    categorys: {
      handler() {
        const temp = {};
        this.categorys.map((item) => {
          const currentPrize = this.prize[item.value];
          temp[item.value] = {};
          if (currentPrize && currentPrize.length > 0) {
            currentPrize.map((prize, index) => {
              temp[item.value][`${index + 1}`] = prize.name;
            });
          }
        });
        this.form = Object.assign({}, this.form, temp);
      },
      immediate: true,
    },
  },
  computed: {
    config: {
      get() {
        return this.$store.state.config;
      },
    },
    prize() {
      return this.$store.state.prize;
    },
    categorys() {
      const options = [];
      for (const key in this.config) {
        if (this.config.hasOwnProperty(key)) {
          const item = this.config[key];
          if (item > 0) {
            let name = conversionCategoryName(key);
            name &&
              options.push({
                label: name,
                value: key,
              });
          }
        }
      }
      return options;
    },
    getCurrentTab() {
      return this.categorys[this.currentTabIndex];
    },
  },
  methods: {
    handleTab(index) {
      this.currentTabIndex = index;
    },
    saveConfig() {
      const prize = {};
      let isValidate = true; // 是否校验成功
      this.categorys.map((cate) => {
        const currentPrize = this.form[cate.value];
        if (!currentPrize) return (isValidate = false);
        const prizeNameList = Object.values(currentPrize).filter(
          (name) => name
        );
        if (prizeNameList.length < this.config[cate.value]) {
          return (isValidate = false);
        }
        // prizeNameList.map((name,index)=>{
        //     prize[cate.value] = {id:null,name}
        // })
        for (let key in currentPrize) {
          const temp = this.prize[cate.value].find(
            (prize) => {
                if(!prize.id) return false
                return prize.id.toString() === key
                }
          );
          if(!prize[cate.value]) prize[cate.value] = []
          const userId = temp?temp.userId:null;
          prize[cate.value].push({
            id: Number(key),
            name: currentPrize[key],
            userId: userId ? userId : null,
          });
        }
      });
      if (!isValidate) return this.$message.error('所有奖项的奖品未填写完整！');
      this.$store.commit('setPrize', prize);
      this.closeDialog();
    },
    closeDialog() {
      this.$emit('closeDialog');
    },
  },
};
</script>

<style lang="scss" scoped>
.prizeList {
  &--title {
    display: flex;
    // height: 2px;
    align-items: flex-end;
    justify-content: space-around;
    padding: 0 10px;
    border-bottom: 2px solid #ffc39f;
    &-tab {
      // display: block;
      width: 76px;
      height: 40px;
      margin-right: 2px;
      text-align: center;
      line-height: 40px;

      color: #ffdfb6;
      font-size: 16px;
      border-radius: 10px 10px 0px 0px;
      background: linear-gradient(360deg, #af0000 0%, #800000 100%);
      cursor: pointer;
      user-select: none;
      &:last-child {
        margin-right: 0;
      }
      &__active {
        width: 88px;
        height: 48px;
        line-height: 48px;

        color: #be0000;
        font-size: 20px;
        font-weight: 600;
        background: linear-gradient(360deg, #ffc29e 0%, #ffe5bc 100%);
      }
    }
  }
  &__container {
    position: relative;
    overflow: auto;
    height: 417px;
    padding-right: 33px;
  }
  &__form {
    height: 298px;
    overflow: auto;
    padding-top: 18px;
    margin-bottom: 36px;
    // border: 1px solid black;
  }
  &__menus {
    display: flex;
    justify-content: space-between;
    margin-left: 33px;
    .menu {
      width: 174px;
      & > img {
        width: 100%;
        cursor: pointer;
      }
    }
  }
}
.form {
  &__line {
    display: flex;
    margin-bottom: 20px;
    padding: 0 10px;
    // &:last-child {
    //   margin-bottom: 36px;
    // }
  }
  &__label {
    width: 107px;
    margin-right: 16px;
    display: flex;
    align-items: center;
    justify-content: right;
    color: #ffd9b1;
    font-size: 16px;
  }
  // &__value {
  //   height: 32px;
  // }
  &__desc {
    margin-left: 12px;
    color: #ff9f9f;
    font-size: 14px;
  }
}
</style>
<style lang="scss">
.prizeList {
  .el-input {
    width: 264px;
    height: 36px;
    .el-input__inner {
      background-color: #fff2e3;
      // color: ffd9b1;
      font-size: 16px;
      font-weight: 600;
      border: none;
      box-shadow: inset 0px 2px 6px 0px rgba(37, 21, 3, 0.5);

      &::-webkit-input-placeholder {
        color: #cdc5bc;
        font-size: 16px;
        font-weight: 400;
      }
    }
  }
}
</style>